<script>
export default {
    install(Vue) {
        Vue.mixin({
            methods: {
                getEventData() {
                    return this.getData("izakaya_event");
                },
                setEventData(data) {
                    this.setData("izakaya_event", data);
                },
                addEvent(event) {
                    const data = this.getEventData();
                    if (!data.list) {
                        data.list = [];
                    }
                    data.list.push(event);
                    this.setEventData(data);
                },
                getEventList() {
                    const data = this.getEventData();
                    if (!data.list) {
                        return [];
                    }
                    data.list.sort((a, b) => {
                        const a_dt = new Date(a.event_date);
                        const b_dt = new Date(b.event_date);
                        // 戻りを逆にして降順にする
                        if (a_dt > b_dt) {
                            return -1;
                        } else if (a_dt < b_dt) {
                            return 1;
                        } else {
                            return 0;
                        }
                    });
                    return data.list;
                },
                getShopList() {
                    const data = this.getEventData();
                    if (!data.shop_list) {
                        return [];
                    }
                    return data.shop_list;
                },
                updateShopList(_shop_list) {
                    const data = this.getEventData();
                    data.shop_list = _shop_list;
                    this.setEventData(data);
                },
                deleteEvent(_event) {
                    const data = this.getEventData();
                    if (!data.list) {
                        return;
                    }

                    const filterData = data.list.filter((event) => {
                        return event.id != _event.id;
                    });
                    data.list = filterData;
                    this.setEventData(data);
                },
                // updateTodo(_todo) {
                //     const data = this.getTodoData();
                //     if (!data.list) {
                //         return;
                //     }

                //     const filterData = data.list.filter((todo) => {
                //         return todo.id == _todo.id;
                //     });
                //     if (filterData.length == 0) {
                //         return;
                //     }
                //     filterData[0].title = _todo.title;
                //     filterData[0].category = _todo.category;
                //     filterData[0].update_date = _todo.update_date;
                //     filterData[0].total = _todo.total;
                //     filterData[0].progress = _todo.progress;
                //     filterData[0].progress_per = _todo.progress_per;

                //     this.setTodoData(data);
                // },
                getData(key) {
                    var data = JSON.parse(localStorage.getItem(key));
                    if (data == null) {
                        return {};
                    }
                    return data;
                },
                setData(key, data) {
                    localStorage.setItem(key, JSON.stringify(data));
                },
            },
        });
    },
};
</script>
